import React, { useState } from "react";
import useAuth from "../../../Hooks/useAuth";
import useIcons from "../../../Assets/web/icons/useIcons";
import Button from "../Button";
import Modal from "../Modal";
import Dropdown from "../Dropdown";
import QLogo from "../../../Assets/web/images/QLogo.svg";

export default function Header() {
  const dropList = {
    component: ({ item }) => <div onClick={() => handleDropClick(item)}>{item?.title}</div>,
    data: [{ title: "Logout", value: "logout" }],
  };
  const [show, setshow] = useState(false);

  const { CallIcon, UserIcon, CaretIcon, BellIcon, LogoutIcon } = useIcons();

  const { clearUser } = useAuth();

  const logout = () => {
    clearUser();
  };

  const handleDropClick = (item) => {
    switch (item.value) {
      case "logout":
        logout();
        break;

      default:
        break;
    }
  };

  const dropEl = (
    <div className="d--flex align-items--center font--sm font--600 gap--sm">
      <Button btnClasses="btn bg--black-100 w-max--36" type="button" variant="primary" icon={<UserIcon width={20} height={20} />} color="black" rounded />
      User Vend
    </div>
  );
  return (
    <header className="w--full p--sm bg--white h-min--50 z-index--sm d--flex align-items--center justify-content--between box-shadow--xs position--relative">
      <div className="w--full  gap--sm d--flex">
        <div className=" d--flex text--primary  justify-content--center  p-l--md">
          <img src={QLogo} alt="" width={35} className="logoImg" />
        </div>
        {/* <Button
          btnClasses="btn gap--sm w-max--100"
          type="button"
          variant="secondary"
          color="white"
          onClick={() => setshow(true)}
          icon={<CallIcon width={18} />}
        >
          Dialer
        </Button> */}
      </div>

      <div className="w--full d--flex justify-content--end align-items--center">
        <Button btnClasses="btn bg--secondary-100 w-max--36" type="button" variant="secondary" icon={<BellIcon width={20} height={20} />} color="secondary" rounded />

        {show && (
          <Modal handleClose={() => setshow(false)}>
            <h1>HI test modal</h1>
          </Modal>
        )}

        <Dropdown closeOnClickOutside={true} dropList={dropList} caretComponent={CaretIcon} showcaret={true}>
          {dropEl}
          <div className="d--flex ">
            {/* <Button
              btnClasses="d--flex align-items--center justify-content--start c--pointer font--600"
              type="button"
              variant="transparent"
              icon={<LogoutIcon width={20} height={20} />}
              color="black"
              onClick={() => logout()}
            >
              <span className="sideBarItem">Logout</span>
            </Button> */}
          </div>
        </Dropdown>
      </div>
    </header>
  );
}
